* {
  font-family: Helvetica, Arial, sans-serif;
}

h2 {
  color: white;
  font-size: 4rem;
  text-align: center;
}

h3 {
  background-image:linear-gradient(#388bfd26, #2ea04326);  
  color: #c9d1d9;
  font-size: 1.7rem;
  text-align: center;
}

p {
  background-color: #21262d;  
  color: white;
  text-align: center;
}

body {
  background-color: #0d1117;
  padding: 60px;
}

input {
  color: gray; 
  font-size: 1.2rem;
  width: 100%;
}

.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.card {
  align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
}

.card p {
  padding: 5px;
  text-align: center;
}

.card img {
  max-height: 170px;
}